.videos {
    background-color: white;
    color: black;
    text-align: center;
    font-size: 25px; 
    border-radius: 10px;
}
.videos h2 {
    font-size: 24px;
    margin: 20px 400px;
}
.videosWrapper {
    display: flex;
    flex-direction: row;
    justify-content: space-around;
    margin-bottom: 30px;
}
.videosWrapper iframe {
    width: 360px;
    height: 220px;
    border-radius: 10px;
}